          title: "김준우🤍진소정 결혼식에 초대합니다.",
          description:
            "2022년 09월 24일 토요일 오후 1시 퀸벨호텔 1층 테라하우스홀",
          imageUrl: "@/assets/0416am300203.jpg",
          link: {
            mobileWebUrl: url,
            webUrl: url,
          },
        },
        // social: {
        //   likeCount: 286,
        //   commentCount: 45,
        //   sharedCount: 845,
        // },
        buttons: [
          {
            title: "청첩장 보기",
            link: {
              mobileWebUrl: url,
              webUrl: url,
            },
          },
        ],
      });
    }

    // 문자 공유하기

    function checkM(m) {
      location.href =
        "sms:" +
        "" +
        (m == "ios" ? "&" : "?") +
        "body=" +
        encodeURIComponent("김준우🤍진소정 결혼식에 초대합니다.\n" + url);
    }

    function checkMobile() {
      var varUA = navigator.userAgent.toLowerCase(); //userAgent 값 얻기
      if (varUA.indexOf("android") > -1) {
        //안드로이드
        return "android";
      } else if (
        varUA.indexOf("iphone") > -1 ||
        varUA.indexOf("ipad") > -1 ||
        varUA.indexOf("ipod") > -1
      ) {
        //IOS
        return "ios";
      } else {
        //아이폰, 안드로이드 외
        return "other";
      }
    }

    // check mobile and kakao pay
    function sendKakaoPay(url) {
      var isMobile =
        /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(
          navigator.userAgent
        );

      if (isMobile) {
        location.href = url;
      } else {
        window.alert("카카오페이는 모바일에서 사용할 수 있습니다.");
      }
    }

    // copy
    function copy(text) {
      copyText(text, undefined, (error, event) => {
        if (error) {
          alert("Can not copy");
          console.log(error);
        } else {
          alert("복사되었습니다.");
          console.log(event);
        }
      });
    }

    // d-day 계산
    function getDday() {
      var today = new Date();
      var day = new Date(2022, 9, 24);
      var gap = day.getTime() - today.getTime();
      var result = Math.ceil(gap / (1000 * 60 * 60 * 24));
      return result;
    }

    // const attrs = {
    //   key: "today",
    //   dates: new Date(),
    //   highlight: true,
    // };
    return {
      parents,
      test,
      imageIndex,
      images,
      junAccountInfo,
      ssoAccountInfo,
      shareKakao,
      sendKakaoPay,
      copy,
      getDday,
    };
  },
};
</script>

